@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style-type: none;
  font-family: 'Josefin Sans', sans-serif;
  font-weight: 600;
}

a {
  color: inherit;
}

.loading {
  text-align: center;
  padding-top: 30vh;
  font-size: 10vw;
  color: rgb(232, 232, 232);
}

::-webkit-scrollbar {
  width: 10px;
  background-color: #272c34;
}

::-webkit-scrollbar-thumb {
  background-color: #171717;
  border-radius: 10px;
}
.navbar {
    color: rgb(240, 240, 240);
    background-color: #171a1f;
    display: flex;
    align-items: center;
    justify-content:space-between;
    flex-direction: column;
  }
  
  .navbar__logo {
    display: flex;
    align-items: center;
   justify-content:center;
   font-size: 1rem;
   margin-top: 1rem;
  }
  
  .navbar__leaf {
    margin-right: 0.6rem;
  }
  
  .navbar__ul {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0.5rem 0;
    grid-gap: 20px;
    gap: 20px;
  }
  
  .navbar__ul li {
    font-weight: 600;
    transition:all 0.5s;
  }
  
  .navbar__ul li:hover {
    font-size: 1.1rem;
    text-shadow: 0px 0px 10px whitesmoke;
  }
  
  .NavbarW__div {
    display: flex;
    font-size: 1rem;
    justify-content: center;
    align-items: center;
    margin-bottom: 0.5rem;
  }
  
  .navbarW__cart {
    padding-right: 0.5rem;
  }
  
  @media(min-width:600px){
    .navbar {
        flex-direction: row;
    }
    .navbar__logo {
        font-size: 1.2rem;
        margin: 0.5rem 0 0.5rem 1rem;
    }
    .NavbarW__div {
        margin: 0 1rem 0 0;
        font-size: 1.2rem;
    }
  }

  @media(min-width:1024px){
    .navbar__ul {
        margin-right: 8vw;
    }
  }

  @media(min-width:1440px){
    .NavbarW__div {
        font-size: 1.5rem;
    }
    .navbar__logo {
        font-size: 1.6rem;
    }
    .navbar__ul {
        margin-right: 12vw;
    }
  }


.card{
    width: 250px;
    height: 400px;
    border: 1px solid black;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: white;
  }
  
  .card__img{
    width: 248px;
    height: 250px;
    border-bottom: 1px black solid;
  }
  
  .card__name {
    margin-top: 0.5rem;
    font-size: 1.35rem;
    font-weight: 500;
  }
  
  .card__hr {
    width: 230px;
    margin: 0.4rem 0;
  }
  
  .card__div {
    display: flex;
    justify-content: space-between;
    width: 200px;
  }
  
  .card__category {
    color: rgb(146, 146, 146);
  }
  
  .card__btn {
    margin-top: 1rem;
    padding: 0.5rem 1rem;
    border: 1px solid black;
    border-radius: 4px;
    font-size: 1.2rem;
    background-color: white;
    transition: all 0.2s ease-in-out;
  }
  
  .card__btn:hover {
    font-size: 1.3rem;
    background-color: rgb(232, 232, 232);
  }
  
  .containercards {
    width: 90%;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    align-items: center;
    justify-items: center;
    grid-gap: 30px;
    gap: 30px;
    padding: 1.5rem 0;
  }
  
  .container {
    background-color:#272c34;
    min-height: 95.3vh;
  }
  
  @media(min-width:600px){
    .containercards {
        grid-template-columns: repeat(2, 1fr);
    }
  }

  @media(min-width:1024px) {
    .containercards {
        grid-template-columns: repeat(3, 1fr);
    }
  }

  @media(min-width:1440px) {
    .containercards {
        grid-template-columns: repeat(5, 1fr);
        width: 95%;
    }
  }
  
  @media(min-width:1440px) {
    .containercards {
        grid-template-columns: repeat(5, 1fr);
        width: 95%;
    }
  }

  @media(min-width:1730px) {
    .containercards {
        grid-template-columns: repeat(6, 1fr);
        width: 95%;
    }
  }

  @media(min-width:2000px) {
    .containercards {
        grid-template-columns: repeat(7, 1fr);
        width: 95%;
    }
  }

.counter {
    display: flex;
    align-items: center;
    justify-content: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-top: 1rem;
  }
  
  .counter__add {
    width: 1.2rem;
    background-color: #272c34;
    border: 0;
    color: rgb(240, 240, 240);
    font-size: 1.5rem;
    transition: 0.2s all ease-in-out;
    border-radius: 5px;
  } 
  
  .counter__add:hover {
    background-color: #414a56;
  } 
  
  .counter__sub {
    width: 1.2rem;
    background-color: #272c34;
    border: 0;
    color: rgb(240, 240, 240);
    font-size: 1.5rem;
    transition: 0.2s all ease-in-out;
    border-radius: 5px;
  }
  
  .counter__sub:hover {
    background-color: #353c46;
  } 
  
  .counter__number {
    width: 10rem;
    height: 23.3px;
    text-align: center;
    font-size: 1.2rem;
  }
  
  .counter__buybutton{
    display: flex;
    border: 0;
    width: 197.9px;
    border-radius: 5px;
    align-items: center;
    justify-content: center;
    margin-top: 0.5rem;
    margin-bottom: 2rem;
    padding: 0.2rem 0;
    background-color:#1a1d23;
    color: rgb(240, 240, 240);
    transition: all 0.2s ease-in-out;
  } 
  
  .counter__buybutton:hover:enabled{
    background-color: #414a56;
    font-size: 1.3rem;
    margin-top: 0;
    cursor: pointer;
  }
  
  .counter__buybutton:disabled {
    background-color: #484f5c;
    color: rgb(127, 127, 127);
    cursor: default;
    box-shadow: 2px 2px 5px #171717;
  } 
  
  .counter__cart {
    padding-left: 0.4rem;
  }
  
.iDC {
    background-color:#272c34;
    padding-top: 1.5rem;
    color: rgb(232, 232, 232);
    min-height: 95.3vh;
  }
  
  .iDC__container {
    max-width: 90%;
    margin: auto;
    display: grid;
    grid-template-columns: 1fr;
    justify-items: center;
  }
  
  .iDC__img {
    border-radius: 10px;
    max-width: 90vw;
    max-height: 400px;
    min-height: 350px;
    background-color: white;
  }
  
  .iDC__div {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    min-width: 10rem;
    min-height: 10rem;
  }
  
  .iDC__name {
    font-size: 2rem;
    margin-top: 1.5rem;
  }
  
  .iDC__hr {
    width: 100%;
    margin: 1rem 0;
  }
  
  .iDC__desc {
    font-size: 1.3rem;
    text-align: center;
  }
  
  .iDC__PyS {
    display: flex;
    width: 95%;
    margin: 0.8rem 0 0.2rem 0;
    align-items: center;
    justify-content: space-between;
  }

  @media(min-width:600px) {
    .iDC__img {
      max-height: 400px;
      min-height: 350px;
    }
  }

  @media(min-width:700px) {
    .iDC__div {
      max-width: 80%;
    }
  }

  @media(min-width:1024px) {
    .iDC__container {
      grid-template-columns: repeat(2, 1fr);
    }
    .iDC__img {
      max-width: 50vw;
    }
    .iDC {
      padding-top: 15vh;
      min-height: 93.6vh;
    }
    .iDC__hr {
      width: 110%;
    }
    .iDC__div {
      margin-left: 3vw;
    }
  }

  @media(min-width:1225px) {
    .iDC__name {
      font-size: 3.4vw;
    }
    .iDC__desc {
      font-size: 1.8vw;
    }
  }

  @media(min-width:1440px) {
    .iDC__img {
      max-width: 70vw;
      max-height: 500px;
      min-height: 350px;
    }
    .iDC {
      padding-top: 7vh;
      min-height: 95vh;
    }
  }

  @media(min-width:2000px) {
    .iDC__img {
      width: 100%;
      max-height: 700px;
      min-height: 500px;
    }
    .iDC__PyS {
      font-size: 1.3vw;
    }
  }

.list__background {
  background-color: #272c34;
  min-height: 95.3vh;
}

.list__container {
  max-width: 90%;
  margin: auto;
  padding-top: 2rem;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  grid-gap: 30px;
  gap: 30px;
  margin-bottom: 1rem;
  justify-items: center;
}

.obj__div {
  background-color: #1a1d23;
  border-radius: 12px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 0.5fr 0.5fr;
  grid-template-areas:
    "img"
    "info"
    "delete";
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  align-self: center;
}
.obj__img {
  height: 170px;
  width: 250px;
  border-radius: 12px 12px 0px 0px;
  grid-area: img;
  background-color: white;
}
.obj__nameQty {
  color: rgb(232, 232, 232);
  grid-area: info;
  font-size: 1.4rem;
  padding: 1rem 0 0 1rem;
}
.obj__price {
  color: rgb(232, 232, 232);
  grid-area: info;
  font-size: 1rem;
  padding: 3rem 0 0 1rem;
}
.obj__totalPrice {
  color: rgb(232, 232, 232);
  grid-area: info;
  font-size: 1rem;
  padding: 4.5rem 0 0 1rem;
}
.obj__remove {
  grid-area: delete;
  background-color: rgb(179, 10, 10);
  border: none;
  color: rgb(244, 208, 208);
  font-size: 1rem;
  width: 135px;
  height: 50px;
  border-radius: 3px;
  align-self: center;
  justify-self: center;
  cursor: pointer;
}
.list__buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 2rem;
  flex-direction: column;
}
.list__clear {
  background-color: rgb(179, 10, 10);
  border: none;
  color: rgb(244, 208, 208);
  font-size: 1rem;
  width: 150px;
  height: 50px;
  border-radius: 5px;

  cursor: pointer;
}
.list__pucharse {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.list__total {
  color: rgb(232, 232, 232);
  font-size: 1.4rem;
}
.list__buy {
  background-color: rgb(189, 255, 202);
  border: none;
  color: rgb(42, 150, 62);
  font-size: 1rem;
  width: 150px;
  height: 50px;
  border-radius: 5px;
  cursor: pointer;
  margin: 0.5rem 0;
}
/* Cuando no hay nada en el carro */
.noCart {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 40vh;
}
.noCart__text {
  font-size: 2rem;
  margin-bottom: 2rem;
  color: rgb(232, 232, 232);
  text-align: center;
}

.noCart__btn {
  border: none;
  border-radius: 5px;
  background-color: #484f5c;
  color: rgb(232, 232, 232);
  font-size: 1.08rem;
  transition: all 0.3s ease-in-out;
  padding: 1rem;
}
.noCart__btn:hover {
  background-color: #656f7b;
  box-shadow: 0px 0px 20px #58606b;
  cursor: pointer;
}

@media (min-width: 425px) {
  .noCart__text {
    font-size: 7vw;
  }
  .noCart__btn {
    font-size: 4vw;
  }
  .noCart {
    height: 60vw;
  }
}

@media (min-width: 590px) {
  .list__container {
    grid-template-columns: repeat(2, 1fr);
  }
  .list__buttons {
    flex-direction: row-reverse;
    width: 85%;
    margin: 2rem auto auto auto;
  }
  .list__clear {
    align-self: flex-end;
  }
  .list__pucharse {
    align-self: flex-end;
  }
  .list__buy {
    margin-bottom: 0;
  }
}

@media (min-width: 900px) {
  .list__container {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 1024px) {
  .noCart__text {
    font-size: 6vw;
  }
  .noCart__btn {
    font-size: 3vw;
  }
  .noCart {
    height: 50vw;
  }
  .list__buttons {
    justify-content: flex-start;
    flex-direction: row;
  }
  .list__pucharse {
    margin-right: 2rem;
  }
}

@media (min-width: 1225px) {
  .list__container {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (min-width: 1490px) {
  .list__container {
    grid-template-columns: repeat(5, 1fr);
  }
}

@media (min-width: 1660px) {
  .noCart__text {
    font-size: 4vw;
  }
  .noCart__btn {
    font-size: 1.5vw;
  }
  .noCart {
    height: 30vw;
  }
}

@media (min-width: 1800px) {
  .list__container {
    grid-template-columns: repeat(6, 1fr);
  }
}

@media (min-width: 2100px) {
  .list__container {
    grid-template-columns: repeat(7, 1fr);
  }
}

@media (min-width: 2480px) {
  .list__container {
    grid-template-columns: repeat(8, 1fr);
  }
}

.background {
    background-color: #272c34;
    min-height: 89vh;
    padding: 1.5rem 0;
}

.checkoutContainer {
    width: 90%;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.checkout {
    padding: 1.2rem 0.8rem;
    background-color: #434a55;
    color: rgb(220, 220, 220);
}

.checkoutInfoContainer {
    padding: 0 0.5rem;
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
}

.checkoutTitle {
    font-size: 1.6rem;
    text-align: center;
}

hr {
    margin: 0.2rem 0 0.8rem 0;
}

/* Comprador */

.checkoutBuyer {
    font-size: 0.95rem;
    display: flex;
    flex-direction: column;
    grid-gap: 3px;
    gap: 3px;
}

.buyerTitle {
    font-size: 1.4rem;
}

/* Lo comprado */

.boughtTitle {
    font-size: 1.4rem;
}

.checkoutItemList {
    background-color: #3c414a;
    padding: 0.6rem 0.5rem;
    grid-gap: 5px;
    gap: 5px;
    display: flex;
    flex-direction: column;
    border: #363c46 solid 1px;
}

.checkoutItem {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

/* Datos extra */

